.section__block{
    max-width: 1400px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 2rem;
}

.first {
    background: #203c34;
}

.second{
    background: #d50032;
}

.third{
    background: #00b388;
}

.third h1, .third h4{
    color: #1e3932 !important;
}

.third a{
    color: #1e3932 !important;
    border: 1px solid #1e3932 !important;
}

.reverse{
    display: flex;
    flex-direction: row-reverse;
}

.section__block img{
    max-width: 100%;
}

.section-container{
    display: flex;
    justify-content: center;
}

.section__block_content{
    text-align: center;
}

.section__block_content h1{
    color: #fff;
    font-size: 50px;
    font-weight: 500;
}

.section__block_content h4{
    color: #fff;
    font-size: 24px;
    font-weight: 400;
    padding-bottom: 1rem;
}

.section__block_content a{
    border-radius: 30px;
    padding: 5px 20px;
    text-decoration: none;
    color: #fff;
    border: 1px solid #fff;
    font-weight: bold;
    background: transparent;
}

@media screen and (max-width: 1100px) {
    .section__block{
        flex-direction: column;
    }

    .section__block_content h1{
        font-size: 30px;
    }

    .section__block_content h4{
        font-size: 20px;
    }
    
    .section__block_img{
        text-align: center;
        width: 75%;
    }

    .section__block_content {
        padding: 1rem;
    }
}