.nav__logo img{
    width: 50px;
}

.nav__links li a{
    list-style: none;
    text-decoration: none;
    color: #000;
    font-weight: bold;
    text-transform: uppercase;
}

.nav__links li a:hover{
    color: #006241;
    transition: 0.5s ease-in;
}

.nav__buttons a:hover{
    color: #006241;
}

.nav__links{
    display: flex;
    list-style: none;
    gap: 1rem;
    flex: 1;
}

.nav__buttons{
    display: flex;
    align-items: center;
    gap: 2rem;
}

.nav__buttons .location{
    text-decoration: none;
    color: #000;
    font-weight: bold;
}

.nav__menu{
    display: flex;
    align-items: center;
    padding: 2rem 5rem;
    gap: 3rem;
}

header{
    box-shadow: 0 2px 2px -2px gray;
}

.nav__links-smallscreen-menu{
    display: none;
}

@media screen and (max-width: 1100px){
    .nav__links{
        display: none;
    }

    .nav__links-smallscreen-menu{
        display: unset;
    }

    .nav__links-smallscreen_overlay{
        display: flex;
    }

    .nav__links-smallscreen li{
        list-style: none;
    }
    
    .nav__links-smallscreen a{
        text-decoration: none;
        color: #000;
    }

    .nav__links-smallscreen a:hover{
        text-decoration: none;
        color: #006241;
    }

    .nav__links-smallscreen_overlay{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: #fff;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        font-size: 2rem;
        text-align: center;
    }
    
    .nav__links-smallscreen li{
        margin-top: 2rem;
    }

    .overlay__close, .overlay__open{
        font-size: 27px;
        cursor: pointer;
        position: absolute;
        top: 30px;
        right: 20px;
    }

    .nav__buttons {
        display: none;
    }

    .nav__menu{
        padding: 1rem;
    }

}