.small__section__block{
    max-width: 1400px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 2rem;
}

.small__section-container{
    display: flex;
    justify-content: center;
}

.small__section__block img{
    width: 100%;
}

.small_section_content{
    text-align: center;
    padding: 2rem;
}

.small_section_content h4{
    font-size: 24px;
    color: #fff;
}

.small_section_content p{
    font-size: 19px;
    color: #fff;
    padding-bottom: 2rem;
}

.small_section_content a{
    border-radius: 30px;
    padding: 5px 20px;
    text-decoration: none;
    color: #fff;
    border: 1px solid #fff;
    font-weight: bold;
}

.first-small{
    background: #006241;
}

.second-small{
    background: #1e3932;
}


@media screen and (max-width: 1100px) {
    .small__section__block{
        flex-direction: column;
    }
}