.footer__links{
    display: flex;
}

.footer__links-block{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: 15%;
}

.footer__links-block h4{
    color: #000;
    font-size: 20px;
    font-weight: 400;
}

.footer__links-block a{
    color: rgba(0,0,0,.58);
    font-size: 15px;
    text-decoration: none;
}

.footer__links-block a:hover{
    color: #000;
}

footer{
    box-shadow: 2px 2px 2px 2px gray;
    padding: 4rem;
    margin-top: 4rem;
}

.footer-line{
    background-color: darkgray;
    margin-top: 2rem;
    width: 100%;
    height: 1px;
}

.footer-icons{
    display: flex;
    gap: 1rem;
    margin-top: 2rem;
}

.footer-icons a{
    color: #000;
}

.footer-icons a:hover{
    opacity: 0.5;
    transition: 0.2s ease-in-out;
}

.footer-bottom-links{
    display: flex;
    gap: 1rem;
    margin-top: 2rem;
}

.footer-bottom-links a{
    text-decoration: none;
    color: #000;
}

.footer-bottom-links a:not(:last-child)::before{
    content:"|"; 
    color: #000;
    padding-right: 1rem;
  }

.footer-bottom-links a:first-child::before{
    content:""; 
    padding-right: 0;
  }

.footer-copyright{
    font-size: 14px;
    color: darkgray;
    margin-top: 2rem;
  }

@media screen and (max-width: 1100px) {
    .footer__links{
        flex-direction: column;
        text-align: center;
    }
    
    .footer__links-block{
        margin: 0 auto;
        margin-top: 1rem;
    }

    footer{
        padding: 1rem;
    }

    .footer-bottom-links{
        flex-direction: column;
        text-align: center;
    }

    .footer-bottom-links a:not(:last-child)::before{
        content:""; 
        color: #000;
        padding-right: 0;
      }
    
    .footer-bottom-links a:first-child::before{
        content:""; 
        padding-right: 0;
    }

    .footer-icons{
        justify-content: center;
    }

    .footer__links-block{
        width: 100%;
    }
}